@import url(https://fonts.googleapis.com/css?family=Nunito);




 /* Large Devices, Wide Screens */
 @media only screen and (min-width : 1200px) {


    h1 {
        font-size: 250%
     }
     
     .first-title-homepage{
         font-family: Nunito;
         
     }
     
     .right-title-homepage{
     width: 45%;
     text-align: left;
     float: right;
     font-size: 130%;
     background-color: rgb(255, 255, 255);
     padding-left: 20px;
     padding-top: 20px;
     padding-bottom: 20px;
     margin-top: 30px;
     }
     .left-title-homepage{
        width: 45%;
        text-align: left;
        float: left;
        font-size: 175%;
        background-color: rgba(7, 3, 129, 0.336);
        padding-left: 20px;
        padding-top: 20px;
        padding-bottom: 20px;
        padding-right: 15px;
    
    
        color: #2c2c2c;
        text-shadow: rgb(2, 0, 54);
        }
    
        
     .slide {
       padding: 10vh 10%;
       min-height: 100vh;
       width: 100vw;
       box-sizing: border-box;
       box-shadow: 0 -1px 10px rgba(0, 0, 0, .7);
         transform-style: inherit;
     }
     
     .slide:nth-child(2n) .title {
       margin-left: 0;
       margin-right: auto;
     }
     
     .slide:nth-child(2n+1) .title {
       margin-left: auto;
       margin-right: 0;
     }
     
     .slide, .slide:before {
       background: 50% 50% / cover;  
     }
     
     .header {
       text-align: center;
       font-size: 175%;
       color: #13046b;
       text-shadow: 0 2px 2px #ffffff;
     }
     
     #title {
       background-image: url("https://imgur.com/4qF9KGb.png");
       z-index:2;
     }
     
     #title h1 {
      transform: translateZ(.25px) scale(.75);
      transform-origin: 50% 100%;
     }





     .square {
 
        width: 150px;
        height: 150px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        left: 50%;
        top: 60%;
        transform: translate(-50%, -50%);
    }
    .home-button a {
        position: relative;
        display: inline-block;
        margin-top: 4px;
        color: #ffffff00;
        font-weight: 800;
        font-size: 25px;
        font-family: 'Nunito', sans-serif;
        color: #15006e;
        font-weight: bold;
    }
    
    .center-title-homepage{

        font-size: 40px;
        margin-top: 20%;
        /* background-color: #e9c0c000;
        padding: 30px;
        position: fixed;
        top: 35%;
        right: 0%;
        transform: translate(-50%, -50%); */
        
    }
 }


