.home-button :hover{

    background-color: #00000000;
    color: #000000e3;
}



.square div:nth-child(1) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid #001b92;
    border-radius: 38% 62% 63% 37% / 41% 44% 56% 59%;
    transition: 0.8s;
    animation: animate 6s linear infinite;
}

.square:hover div:nth-child(1) {
    border: none;
    background: rgba(255, 255, 255, 0);
}

.square div:nth-child(2) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid #01004b;
    border-radius: 38% 62% 63% 37% / 41% 44% 56% 59%;
    transition: 0.8s;
    animation: animate2 4s linear infinite;
}

.square:hover div:nth-child(2) {
    border: none;
    background: rgb(16, 0, 85);
}

.square div:nth-child(3) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid #02005c;
    border-radius: 38% 62% 63% 37% / 41% 44% 56% 59%;
    transition: 0.8s;
    animation: animate 10s linear infinite;
}

.square:hover div:nth-child(3) {
    border: none;
    background: rgb(0, 104, 202);
}

@keyframes animate
{
    0%
    {
        transform: rotate(0deg);
    }
    100%
    {
        transform: rotate(360deg);
    }
}

@keyframes animate2
{
    0%
    {
        transform: rotate(360deg);
    }
    100%
    {
        transform: rotate(0deg);
    }
}




