.response-list{

align-items: baseline;

}

  .loading-state{
    color: rgb(0, 0, 0);
    padding-top: 200px;
  }


.click-link{

color: #000;
margin-left: 50%;
display: flex;
flex-wrap: wrap;
align-content: center;


}

.status-link{

  color: #000;
  margin-left: 46%;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  font-size: 25px;
  
  
  }

  .card{
    /* other default style */
    grid-row: 2/3;
    grid-column: 2/3;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 1fr 6fr 1fr;
  }

  .card div{
    grid-row: 2/3;
  }


  .button {
    background-color: #f4511e;
    border: none;
    color: white;
    padding: 16px 32px;
    text-align: center;
    font-size: 16px;
    margin: 4px 2px;
    opacity: 0.6;
    transition: 0.3s;
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
  }
  
  .button:hover {opacity: 1}



  .res-button{

    color: #000;
    height: 50px;
    align-items: center;
    border-radius: 15px;



  }


  .res-button:hover{

    color: #000;
    height: 50px;
    align-items: center;
    border-radius: 15px;
    outline: none; 
    border-color: #ffffff00;


  }

